<template>
  <div class="status-wrapper">
    <div class="status row">
      <div class="status-item col-xs" v-for="n in total">
        <div
          :class="getClass(n)"
          @click="goTo(n)"
        >
        </div>
      </div>
    </div>
    <div class="lines">
      <div class="line"></div>
      <div class="line"></div>
      <div class="line"></div>
      <div class="line"></div>
      <div class="line"></div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    total: Number,
    current: Number,
    goTo: {
      type: [Function, Boolean],
      default: false,
    },
  },
  methods: {
    getClass(n) {
      const currentIndex = this.$route.params.id;
      if (n < currentIndex) return 'circle answered';
      if (n > currentIndex) return 'circle unanswered';
      return 'circle';
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../scss/settings';
.status-wrapper {
  padding: 2em;
  padding-bottom: 0.5em;
}

.status {
  margin-bottom: -1.4em;
  position: relative;
  z-index: 1;
}

.lines {
  width: 100%;
  opacity: 0.4;
}

.line {
  width: 100%;
  height: 0.125em;
  background: $yellow;
  margin-top: 0.2em;
}

.circle {
  margin: 0 auto;
  border: 4px solid $yellow;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  transition: transform 0.2s ease,
    border-color 0.2s ease,
    background 0.2s ease;
  box-sizing: border-box;
}

.circle.answered {
  background: $yellow;
  cursor: pointer;
}

.circle.unanswered {
  border-color: $light-gray;
}

.circle.answered:hover {
  background: transparent;
  transform: scale(1.2);
  transition: transform 0.2s ease,
    background 0.2s ease;
}

.man-move-enter-active, .man-move-leave-active {
  transition: opacity 0.2s ease;
}

.man-move-enter, .man-move-leave-active {
  opacity: 0;
}
</style>



