<template>
  <div>
    <transition-manager>
      <div class="result" v-if="ready">
        <h1>你心中的那首歌是</h1>
        <h2 class="result-title">{{result.title}}</h2>
        <div class="result-image">
          <img src="http://placehold.it/1200x630" />
        </div>
        <result-meter :score="result.score"></result-meter>
        <div class="result-msg">
          <h3>{{result.msg}}</h3>
        </div>
        <div class="action">
          <btn action="">分享結果</btn>
          <btn route="/">再玩一次</btn>
          <btn action="">了解更多</btn>
        </div>
      </div>
      <div class="holder" v-else>
        <div class="dot"></div>
        <div class="dot"></div>
        <div class="dot"></div>
      </div>
    </transition-manager>
    <music-player @musicReady="ready = true" :music="result.music"></music-player>
  </div>
</template>

<script>
import { reduce } from 'lodash';
import Btn from '../components/Btn';
import ResultMeter from '../components/ResultMeter';
import MusicPlayer from '../components/MusicPlayer';
import TransitionManager from '../components/TransitionManager';
import results from '../data/results';

export default {
  beforeMount() {
    if (!Object.keys(this.$store.state.answer).length) {
      this.$router.push('/');
    }
  },
  data() {
    return {
      ready: false,
    };
  },
  components: {
    Btn,
    MusicPlayer,
    ResultMeter,
    TransitionManager,
  },
  computed: {
    result() {
      const {
        answer,
        valued,
      } = this.$store.state;
      if (Object.keys(answer.score).length < 2) return results[3];
      /* eslint-disable no-param-reassign */
      const sum = reduce(answer.score, (result, value) => {
        result = (result + value) || value;
        return result;
      }, 0);
      /* eslint-enable no-param-reassign */
      switch (sum) {
        case 1:
        case 2:
        case 3:
          return results[4];
        case 4:
          return results[5];
        case -1:
        case -2:
        case -3:
          return results[2];
        case -4:
          return results[1];
        case 0:
          return results[answer.score[valued] + 3];
        default:
          return results[3];
      }
    },
  },
};
</script>

<style scoped lang="scss">
@import '../scss/settings';

.holder {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

$animation-speed: 1s;
$dot-size: 1em;

@keyframes fx {
  50% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@mixin animation($delay: 0ms) {
  animation: fx $animation-speed ease infinite $delay;
}

.dot {
  width: $dot-size;
  height: $dot-size;
  border: ($dot-size / 5) solid $yellow;
  border-radius: 50%;
  float: left;
  margin: 0 ($dot-size / 2);
  transform: scale(0);
  @include animation();

}

.dot:nth-child(2) {
  @include animation($animation-speed * 0.3);
}

.dot:nth-child(3) {
  @include animation($animation-speed * 0.6);
}

.result-title {
  color: $dark-gray;
}

.result-image {
  margin: 1em auto;
}



</style>



