<template>
  <transition
    @before-leave="beforeLeave"
    @before-enter="beforeEnter"
    @after-enter="afterEnter"
  >
    <slot></slot>
  </transition>
</template>

<script>
export default {
  /* eslint-disable no-param-reassign */
  methods: {
    afterEnter(el) {
      this.$store.commit('transitionEnd');
      el.className = this.className;
    },
    beforeEnter(el) {
      this.className = el.className;
      const animateClassName = this.$router.direction === 'next' ? 'fadeInRight' : 'fadeInLeft';
      el.className += ` animated ${animateClassName}`;
    },
    beforeLeave(el) {
      this.$store.commit('transitionStart');
      const animateClassName = this.$router.direction === 'next' ? 'fadeOutLeft' : 'fadeOutRight';
      el.className = `${this.className} animated ${animateClassName}`;
    },
  },
};
</script>

<style scoped lang="scss">

</style>



