<template>
  <div id="player" class="music-player">

  </div>
</template>

<script>
function calcStartSeconds(string) {
  const minute = string.split('m');
  if (minute.length === 1) return parseInt(string, 10);
  const second = minute[1].split('s');
  return (minute[0] * 60) + (second[0] * 1);
}

export default {
  mounted() {
    let done = false;

    const onPlayerReady = (event) => {
      event.target.seekTo(calcStartSeconds(this.music.start)).playVideo();
      this.$emit('musicReady');
    };

    const stopVideo = () => {
      this.player.stopVideo();
    };

    const onPlayerStateChange = (event) => {
      if (event.data === window.YT.PlayerState.PLAYING && !done) {
        setTimeout(stopVideo, this.music.duration * 1000);
        done = true;
      }
    };

    this.player = new window.YT.Player('player', {
      height: '0',
      width: '0',
      videoId: this.music.id,
      events: {
        onReady: onPlayerReady,
        onStateChange: onPlayerStateChange,
      },
    });
  },
  props: {
    music: Object,
  },
};
</script>

<style scoped>
.music-player {
  display: none;
  visibility: hidden;
}
</style>



