import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

/* eslint-disable no-param-reassign */
const store = new Vuex.Store({
  state: {
    answer: {},
    inTransition: false,
  },
  mutations: {
    answer(state, payload) {
      if (typeof state.answer[payload.type] === 'undefined') state.answer[payload.type] = {};
      state.answer[payload.type][payload.key] = payload.value;
    },
    noteValued(state, payload) {
      state.valued = payload.key;
    },
    transitionStart(state) {
      state.inTransition = true;
    },
    transitionEnd(state) {
      state.inTransition = false;
    },
    reset(state) {
      state.answer = {};
    },
  },
});

export default store;



