<template>
  <div class="meter-wrapper">
    <svg id="result-meter" preserveAspectRatio="xMinYMin meet"></svg>
  </div>
</template>

<script>
import SVG from 'svg.js';

const scores = {
  0: '超級受害',
  1: '受害',
  2: '受益',
  3: '超級受益',
};
export default {
  mounted() {
    const em = parseFloat(getComputedStyle(this.$el).fontSize);
    const width = this.$el.offsetWidth;
    const margin = em * 2.5;
    const gap = (width - (margin * 2)) / 3;

    const draw = new SVG('result-meter').viewbox(0, 0, width, width / 8);
    draw.line(0, 2 * em, width, 2 * em).stroke({ width: 1 });
    for (let i = 0; i < 4; i += 1) {
      const xPos = margin + (gap * i);
      draw.line(xPos, 1.5 * em, xPos, 2.5 * em).stroke({ width: 1 });
      const text = draw.text(scores[i]).move(xPos, 2.5 * em);
      if (this.score === i) {
        text.addClass('is');
        draw.text('v').move(xPos, 0).addClass('marker');
      }
    }
  },
  props: {
    score: [Number, Boolean],
  },
};
</script>

<style lang="scss">
@import '../scss/settings';
.meter-wrapper {
  margin-bottom: 1em;
  text-align: center;
}

#result-meter {
  text {
    font-family: $fonts;
    text-anchor: middle;
    font-size: 1em;
    fill: $light-gray;
  }

  text.is {
    fill: $dark-gray;
  }

  text.marker {
    fill: $yellow;
    font-size: 1.5em;
  }
}
</style>



